define('xtalus/components/md-5', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    value: null,
    md5: (function () {
      if (Ember['default'].isEmpty(this.get('value'))) {
        return "";
      }
      return md5(this.get('value'));
    }).property('value')
  });

});